import React from 'react';
import { useQueryClient } from 'react-query';
import { StatusesInterface } from '../../../../store/reducers/statuses-reducer';
import "./SkillCoinDescription.css"
interface ISkillCoinDescriptionProps {
    description:string;
}

export const SkillCoinDescription:React.FC<ISkillCoinDescriptionProps> = ({description}) => {
    const statuses = useQueryClient().getQueryData('statuses') as StatusesInterface[];
    
    const coditionsMap = {
        "[onhit]" : "[При Попадании]",
        "[clashwin]" : "[При Победе в Столкновении]",
        "[combatstart]" : "[Начало Боя]",
        "[clashlose]" : "[При Проигрыше в Столкновении]",
        "[onuse]" : "[При Использовании]",
        "[onkill]" : "[При Убийстве]",
        "[onevade]" : "[При Уклонении]",
        "[headshit]" : "[Выпала Монетка]",
        "[hitafterclashwin]" : "[Попадание После Победы в Столконовении]",
        "[afterattack]" : "[После Атаки]",
        "[beforeattack]" : "[Перед Атакой]",
        "[reuse-headshit]" : "[Повторное Применение - Выпала Монетка]",
        "[tailshit]" : "[Не Выпала Монетка]",
        "[oncrit]" : "[При Критическом Попадании]",
    }
    const coinFunc = (value:string,result:string[]) =>{
        if(isNaN(+value)) return value;
        return `${result.length && "</br>" || ""}<img class="coin" src="../images/general/coin${value}.png" alt="coinN"/>`
    }
    const conditionFunc = (value:string,result:string[]) =>{
        const valueAsKey = value.toLowerCase().replace(/\s/g, '');
        if( valueAsKey in coditionsMap) 
        return `<span class="condition condition--${valueAsKey.substr(1,valueAsKey.length-2)}">${coditionsMap[valueAsKey as keyof typeof coditionsMap]}</span>`;
        return "";
    }
    const tagFunc = (value:string,result:string[]) =>{
        const status = statuses.find( s => s.id === value);
        if(!status) return value;
        const {name,description} = status;
        return `<img class="status" src="../images/tags/${value}.png" alt="${value}" /><span class="status-name status-name--${value} tooltip-container tooltip--status">${name}<span class="entityFullInfo-tooltip tooltip-container--status">${description}</span></span>`
    }
    const specialsMap = {
        "@":coinFunc,
        "$":conditionFunc,
        "#":tagFunc,
    }
    
    const  f = (str:string) =>{
        let result:string[] = [];
        let tracked = {value:"",index:-1};
        for(let i = 0 ; i < str.length;i++){
            const currentChar = str[i];
            if(currentChar in specialsMap){
                if(currentChar === tracked.value){
                    const variableVal = str.substring(tracked.index + 1, i);
                    const func = specialsMap[currentChar as keyof typeof specialsMap];
                    const textVal = func(variableVal,result);
                    if(textVal !== "") result.push(textVal);
                }else{
                    const textVal = str.substring(tracked.index + 1, i);
                    if(tracked.value !=="@" && textVal !== "") result.push(textVal);
                }
                tracked = { value:currentChar,index:i};
            }
        }
        const textVal = str.substring(tracked.index + 1, str.length);
        result.push(textVal);
        const firstPart = result[0];
        if(firstPart && firstPart.substring(0,4) === "")  result[1] = firstPart.substr(0, (firstPart.length - 5) )
        return result.join("");
    }
  return (
    <p className={`${'skill-coin-description'}`} dangerouslySetInnerHTML={{ __html: f(description) }}/>
  );
};