import React, { useRef } from 'react';
import { IdentityInterface } from '../../../../store/reducers/ids-reducer';
import { ISkill } from '../EntityFullInfoSkills';
import "./EntityFullInfoSkill.css"
import { SkillCoinDescription } from '../skill-coin-description/SkillCoinDescription';
import useHover from '../../../../hooks/useHover';
interface IEntityFullInfoProps {
    identity:IdentityInterface;
    skill:ISkill;
}
export const EntityFullInfoSkill:React.FC<IEntityFullInfoProps> = ({identity,skill}) => {
    const {imgUrl,countCoin,basicCoin,weightCoin,growthPerCoin,nameSkill,damage,descriptionCoin} = identity;
    const testDescription = descriptionCoin.includes("|") && descriptionCoin.split("|")[skill.index] || "";
    const resourceCount =  skill.index === 0 && 3 || skill.index === 2 && 1 || 2;
    const coins = Array(countCoin[skill.index]).fill(0);
    return (
        <article className={`${'entityFullInfo-skill'}`} >
                    <span className={`${'entityFullInfo-skill-index'}`} >{skill.index !== 3 && `Навык ${skill.index+1}`|| "Защитный навык"} </span>
                    
                    <div className={`${'entityFullInfo-skill-r'}`}>
                        <div style={{display:"flex",flexWrap:"wrap",alignItems:"center",gap:"20px",marginBottom:"15px"}}>
                            <p className={`${'skill-name'} ${skill.sin}-sin-color`} >{nameSkill[skill.index]}</p>
                            <span className={`${'skill-count'} tooltip-container`} >
                                { skill.index !== 3 && `x${resourceCount}`}
                                <span className={`${'entityFullInfo-tooltip'}`} >Кол-во в ротации из 6 навыков</span>
                            </span>
                            <span className={`${'skill-baseCoin'} tooltip-container coin-text`} >{basicCoin[skill.index]}
                                    <span className={`${'entityFullInfo-tooltip'}`} >Базовое значение</span>
                            </span>
                            <div className='entityFullInfo-coins' >
                                <span className={`${'skill-coinGrowth'} tooltip-container`} >+{growthPerCoin[skill.index]}
                                    <span className={`${'entityFullInfo-tooltip'}`} >Базовый прирост за монету</span>
                                </span>
                                    {
                                    coins.map(
                                        (element,index)=>{
                                            return <img key={index} src={`../images/general/coin.png`} alt={`coin`}/>
                                        }
                                    )
                                    }
                            </div>
                            </div>
                        <div style={{display:"flex",flexWrap:"wrap",alignItems:"center",gap:"20px",marginBottom:"15px"}}>
                            <div className={`${'skill-atk'} tooltip-container`} >
                                <img src={`../images/general/${skill.dmgType}.png`} alt={`${skill.dmgType}`}/>
                                <span className={`${'entityFullInfo-tooltip'}`} >{ skill.index === 3 && "Тип защиты" || "Тип урона"}</span>
                            </div>
                            <div className={`${'skill-atk'} tooltip-container`} >
                                <img src={`../images/sins/${skill.sin}.png`} alt={`${imgUrl}`}/>
                                <span className={`${'entityFullInfo-tooltip'}`} >Грех</span>
                            </div>
                            
                            <div className={`${'skill-atk'} tooltip-container`} >
                                {basicCoin[skill.index] + growthPerCoin[skill.index]*countCoin[skill.index]}
                                <img src={`../images/general/coinBefore.png`} alt={`${imgUrl}`}/>
                                <span className={`${'entityFullInfo-tooltip'}`} >Суммарное значение монет</span>
                            </div>
                            <div className={`${'skill-atk'} tooltip-container`} >
                                {damage[skill.index]}
                                <img src={`../images/general/${ skill.index !== 3 && `damage` || `evadeDef1`}.png`} alt={`${imgUrl}`}/>
                                <span className={`${'entityFullInfo-tooltip'}`} >{ skill.index === 3 && "Уровень защиты" || "Уровень атаки"}</span>
                            </div>
                            <div className={`${'skill-weight'} tooltip-container`} >
                                {new Array(weightCoin[skill.index]).fill(0).map((e,index)=><div key={index}/>) }
                                <span className={`${'entityFullInfo-tooltip'}`} >Кол-во слотов, по которым бьет навык</span>
                            </div>
                        </div>
                        
                        {
                                testDescription.split("|").map(
                                    (element,index)=>{
                                        return <SkillCoinDescription key={index} description={element} />
                                    }
                                )
                            }
                    </div>
            </article>
    );
};