import React, { useEffect, useLayoutEffect, useRef, useState } from 'react';
import { useQueryClient } from 'react-query';
import { IdentityInterface } from '../../../store/reducers/ids-reducer';
import { StatusesInterface } from '../../../store/reducers/statuses-reducer';
import { getStatusesEntityList } from '../../../tools/getStatusesEntityList';
import "./EntityFullInfoIdentity.css"
interface IEntityFullInfoProps {
    identity:IdentityInterface;
}
const resMap:{
    [key:string]:string
} = {
    'ineff':'[x0.5]',
    'normal':'[x1]',
    'fatal':'[x2]'
}
export const EntityFullInfoIdentity:React.FC<IEntityFullInfoProps> = ({identity}) => {
    const {imgUrl,idTier,hp,hpStun,speed,defence,slash,pierce,blunt,rarity,sinner,descriptionCoin,descriptionPassive1,descriptionPassive2} = identity;
    const statuses = useQueryClient().getQueryData('statuses') as StatusesInterface[];
    const rarityStyled = rarity.replaceAll("O","Ø");
    const status = getStatusesEntityList([descriptionCoin,descriptionPassive1,descriptionPassive2]);
    return (
        <section className={`${'entityFullInfo-entity'}`} >
                <article  
                    className={"entityFullInfo-img"}>
                    <div className="entityFullInfo-rarity" >{rarityStyled}</div>
                    <img className="entityFullInfo-sinner" src={`../images/sinners-icons/${sinner}.webp`} alt={`${sinner}`}/>

                    <div className={"shadow"}>
                        <img src={`../images/identities-profiles/${imgUrl}_profile.png`} alt={`${imgUrl}`}/>
                    </div>
                    <div className={`entityFullInfo-tier entityFullInfo-tier-${idTier}`}>
                        {idTier}
                    </div>
                </article>
                <div  className={`${'entityFullInfo-stats-container'}`} >
                {
                    status && <article className={"entityFullInfo-stats"}>
                    <h2 >Cтатусы</h2>
                    <ul className='statuses-list'>
                        {
                            Object.keys(status).map( (s,index) =>{
                                const status = statuses.find( st => st.id == s);
                                if(!status) return null;
                                const {description} = status;
                                return <li key={index} className='tooltip-container '>
                                    <img src={`../images/tags/${s}.png`} alt={`${s}`}/>
                                    <span className='entityFullInfo-tooltip '>{description}</span>
                                </li>
                            })
                        }
                    </ul>
                </article>
                }
                
                <article className={"entityFullInfo-stats"}>
                    <h2 >Статы</h2>
                    <ul>
                        <li>
                            <img src={`../images/general/hp.png`} alt={`${imgUrl}`}/>
                            {hp}
                        </li>
                        <li>
                            <img src={`../images/general/speed.png`} alt={`${imgUrl}`}/>
                            {speed}
                        </li>
                        <li>
                            <img src={`../images/general/defence1.png`} alt={`${imgUrl}`}/>
                            {defence}
                        </li>
                    </ul>
                </article>
                <article className={"entityFullInfo-stats"}>
                    <h2>Сопротивления</h2>
                    <ul>
                        <li className={`resistance--${blunt}`} >
                            <img src={`../images/dmg-type/blunt.png`} alt={`${imgUrl}`}/>
                            {resMap[blunt]}
                        </li>
                        <li className={`resistance--${pierce}`}>
                            <img src={`../images/dmg-type/pierce.png`} alt={`${imgUrl}`}/>
                            {resMap[pierce]}
                        </li>
                        <li className={`resistance--${slash}`}>
                            <img src={`../images/dmg-type/slash.png`} alt={`${imgUrl}`}/>
                            {resMap[slash]}
                        </li>
                    </ul>
                </article>
                <article className={"entityFullInfo-stats"}>
                    <h2>Порог Стаггера</h2>
                    <ul className='staggers'>
                        {
                            hpStun.map(
                                (e,index)=>{
                                    return <li key={index}>
                                        {e} ({Math.round(e/hp *100)}%)
                                    </li>
                                }
                            )
                        }
                    </ul>
                </article>
                </div>
        </section>
    );
};